import Vue from "vue";
import App from "./App";

const app = new Vue({
  template: '<App  ref="productconfig"/>',
  components: {
    App
  }
}).$mount("#app");

$(function () {
  $("#saveProductInfo").click(function () {
    app.$refs["productconfig"].saveDatas();
  });
});



