ng\",\r\n                                        center: true,\r\n                                        showCancelButton: false\r\n                                    });\r\n                                }\r\n                                else {\r\n                                    that.$message({\r\n                                        message: \"销售配置信息保存成功！\",\r\n                                        type: \"success\"\r\n                                    });\r\n                                }\r\n                            } else {\r\n                                that.$confirm(result.ErrorMsg, \"保存失败\", {\r\n                                    confirmButtonText: \"确定\",\r\n                                    type: \"warning\",\r\n                                    center: true,\r\n                                    showCancelButton: false\r\n                                });\r\n                            }\r\n                        }).catch(err => {\r\n                            that.$message({\r\n                                type: 'error',\r\n                                message: err\r\n                            });\r\n                            loading && loading.close();\r\n                        });\r\n                }\r\n            });\r\n        }\r\n    }\r\n}\r\n</script>\r\n<style scoped>\r\n[v-cloak] {\r\n  display: none;\r\n}\r\n\r\n.el-form-item {\r\n  margin-bottom: 16px;\r\n}\r\n\r\n.el-radio__label {\r\n  padding-left: 0px;\r\n}\r\n\r\n.el-checkbox-group .el-checkbox {\r\n  margin-left: 0;\r\n  padding-right: 10px;\r\n}\r\n\r\n.el-checkbox-group .el-checkbox {\r\n  margin-left: 0;\r\n  padding-right: 10px;\r\n}\r\n</style>\r\n"],"sourceRoot":""}]);

