<template>
    <glui-form :model="formData" :rules="rules" ref="formData" label-width="130px">
        <glui-form-item label="销售渠道" style="padding-top:18px;" required>
            <glui-row>
                <glui-col :span="24" prop="ChannelList">
                    <glui-form-item>
                        <glui-checkbox v-on:change="handleCheckAllChange">全选</glui-checkbox>
                        <glui-checkbox-group v-model="formData.ChannelList">
                            <glui-checkbox v-for="item in platsource" :label="item.id" :key="item.id">{{item.name}}</glui-checkbox>
                        </glui-checkbox-group>
                    </glui-form-item>
                </glui-col>
            </glui-row>
            <glui-row v-if="showSubPlat">
                <glui-col :span="24">
                    <glui-form-item prop="SubChannels">
                        <glui-checkbox-group v-model="formData.SubChannels">
                            <glui-checkbox v-for="item in subplatsource" :label="item.id" :key="item.id">{{item.name}}</glui-checkbox>
                        </glui-checkbox-group>
                    </glui-form-item>
                </glui-col>
            </glui-row>
        </glui-form-item>
        <glui-form-item label="推送营销系统" prop="IsMarketing">
            <glui-radio-group v-model="formData.IsMarketing">
                <glui-radio :label="1">是</glui-radio>
                <glui-radio :label="0">否</glui-radio>
            </glui-radio-group>
        </glui-form-item>
        <glui-form-item label="是否搜索显示" prop="IsShowSearch">
            <glui-radio-group v-model="formData.IsShowSearch">
                <glui-radio :label="1">是</glui-radio>
                <glui-radio :label="0">否</glui-radio>
            </glui-radio-group>
        </glui-form-item>
        <glui-form-item label="立减供应商承担" v-if="formData.Belong!=1" prop="SupplierCommitment">
            <glui-col :span="4">
                <glui-radio-group v-model="formData.SupplierCommitment">
                    <glui-radio :label="1">是</glui-radio>
                    <glui-radio :label="0">否</glui-radio>
                </glui-radio-group>
            </glui-col>
            <glui-col :span="18">
                备注：默认否，不参加立减供应商承担比例>0的优惠，如果选择是，必须填写优惠费用承担商和最大承担比例
            </glui-col>
        </glui-form-item>
        <glui-form-item label="优惠费用承担商" v-if="formData.Belong!=1&&formData.SupplierCommitment==1" prop="checkSupplierResource">
            <glui-col :span="8">
                <glui-select v-model="checkSupplierResource" placeholder="请选择">
                    <glui-option v-for="item in suppliersource" :key="item.SupplierId+','+item.ResourceType+','+item.ResourceId" :label="supplierfomart(item)" :value="item.SupplierId+','+item.ResourceType+','+item.ResourceId">
                    </glui-option>
                </glui-select>
            </glui-col>
            <glui-col :span="16">
                备注：优惠后台配置承担小于等于你这里配置的比例，优惠才生效，否则产品将不参加该优惠
            </glui-col>
        </glui-form-item>
        <glui-form-item label="最大承担比例" v-if="formData.Belong!=1&&formData.SupplierCommitment==1" prop="SupplierRatio">
            <glui-col :span="4">
                <glui-input placeholder="请输入数字" v-model.number="formData.SupplierRatio">
                    <template slot="append">
                        %
                    </template>
                </glui-input>
            </glui-col>
            <glui-col :span="18">
                备注：选择你产品下面的一家供应商，可能用于承担优惠费用
            </glui-col>
        </glui-form-item>
        <glui-form-item label="是否支持红包" v-if="formData.Belong!=3" prop="RedPrivilege">
            <glui-radio-group v-model="formData.RedPrivilege">
                <glui-radio :label="1">是</glui-radio>
                <glui-radio :label="0">否</glui-radio>
            </glui-radio-group>
        </glui-form-item>
        <glui-form-item label="旅仓利润点" v-if="formData.Belong!=3&&showProfit" prop="NewWarehouseProfit">
            <glui-col :span="3">
                <glui-input placeholder="请输入数字" width="10px;" v-model.number="formData.NewWarehouseProfit">
                    <template slot="append">
                        %
                    </template>
                </glui-input>
            </glui-col>
            <glui-col :span="18">
                &nbsp;&nbsp;注意：旅仓利润点默认为5%
            </glui-col>
        </glui-form-item>
        <glui-form-item label="是否验证营收" v-if="formData.Belong!=3" prop="IsCheckRevenue">
            <glui-radio-group v-model="formData.IsCheckRevenue">
                <glui-radio :label="1">是</glui-radio>
                <glui-radio :label="0">否</glui-radio>
            </glui-radio-group>
        </glui-form-item>
        <glui-form-item label="是否置顶" v-if="formData.Belong==3">
            <glui-col :span="4">
                <glui-radio-group v-model="formData.IsTop">
                    <glui-radio :label="1">是</glui-radio>
                    <glui-radio :label="0">否</glui-radio>
                </glui-radio-group>
            </glui-col>
            <glui-col :span="3" v-if="formData.Belong==3&&formData.IsTop==1">

                <glui-form-item prop="SortNo">
                    <glui-input placeholder="排序值，越大越靠前" v-model.number="formData.SortNo"></glui-input>
                </glui-form-item>
            </glui-col>
        </glui-form-item>
        <glui-form-item label="百旅会权益" v-if="formData.Belong==3&&showBl" prop="TagInfoList">
            <glui-col :span="4">
                <glui-checkbox-group v-model="formData.TagInfoList">
                    <glui-checkbox v-for="item in tagsource" :label="item.TagInfoId" :key="item.TagInfoId">{{item.TagInfoName}}</glui-checkbox>
                </glui-checkbox-group>
            </glui-col>
            <glui-col :span="4">
                注意：最多选择{{blMax}}项！
            </glui-col>
        </glui-form-item>
        <glui-form-item label="权益下单设置" v-if="formData.Belong==3&&showPower" prop="PowerCount">

            <glui-col :span="4">
                <glui-select v-model="formData.PowerCount" clearable placeholder="请选择">
                    <glui-option v-for="item in powersource" :key="item.value" :label="item.text" :value="item.value">
                    </glui-option>
                </glui-select>
            </glui-col>
            <glui-col :span="18">
                （选择1，即用户支持抵扣1次权益下单）
            </glui-col>
        </glui-form-item>

        <glui-form-item label="创新项目" v-if="formData.Belong==3">
            <glui-col :span="4">
                <glui-select v-model="formData.Innovation" clearable placeholder="请选择">
                    <glui-option v-for="item in innovationsource" :key="item.id" :label="item.text" :value="item.id">
                    </glui-option>
                </glui-select>
            </glui-col>
            <glui-col :span="18">注意：仅缘分大巴与亲子项目勾选此处！</glui-col>

        </glui-form-item>
        <glui-form-item label="是否验证酒店" v-if="formData.Belong==2" prop="IsCheckHotel">
            <glui-col :span="4">
                <glui-radio-group v-model="formData.IsCheckHotel">
                    <glui-radio :label="1">是</glui-radio>
                    <glui-radio :label="0">否</glui-radio>
                </glui-radio-group>
            </glui-col>
            <glui-col :span="18">
                备注：动态自由行产品是否验证酒店;选择"是"，请务必录入酒店资源，选择"否"，可以不配置酒店资源。
            </glui-col>
        </glui-form-item>
    </glui-form>
</template>
<script>
import config from "./config";
import httputils from "@/js/httputils";
export default {
    data() {
        return {
            productId: $("#productid").val(),
            blMax: 1,
            innovationsource: config.innovativeProject,
            powersource: Array.from({ length: 4 }, (v, k) => {
                var s = k + 1;
                return { value: s, text: s };
            }),
            TagId: $("#producttagid").val(),
            tagsource: [],
            suppliersource: [],
            checkSupplierResource: "",
            formData: {
                ProductId: null,
                Channels: 0,
                IsShowSearch: 1,
                IsTop: 0,
                SortNo: 0,
                IsMarketing: 1,
                PowerCount: null,
                Innovation: null,
                Belong: 2,
                ProductAttribute: 0,
                NewWarehouseProfit: Number($("#profit").val()),
                SupplierCommitment: 0,
                CommitmentSupplierId: 0,
                SupplierRatio: 0.0,
                RatioResourceType: 0,
                RatioResourceId: 0,
                RedPrivilege: 0,
                IsCheckRevenue: 1,
                IsCheckHotel: 1,
                TagInfoList: [],
                ChannelList: config.getPlatIds(),
                SubChannels: config.getSubPlatIds(),
            },
            rules: {
                ChannelList: [
                    {
                        validator: function (rule, value, callback) {
                            if (!value || value.length <= 0) {
                                return callback(new Error("此处不可为空"));
                            }
                            callback();
                        },
                        trigger: "change"
                    }
                ],
                SubChannels: [{ required: true, message: "此处不可为空", trigger: "change" }],
                CommitmentSupplierId: [
                    { required: true, message: "此处不可为空", trigger: "change" }
                ],
                SupplierRatio: [
                    {
                        validator: function (rule, value, callback) {
                            var re = /^([1-9][0-9]?|100)$/;
                            if (!re.test(value)) {
                                return callback(new Error("优惠金额最大承担比例必须为1到100的整数！"));
                            }
                            callback();
                        }, trigger: "blur"
                    }
                ],
                NewWarehouseProfit: [
                    { required: true, message: "此处不可为空" },
                    { type: "number", message: "旅仓利润点必须为数字" }
                ],
                SortNo: [
                    { required: true, message: "此处不可为空" },
                    { type: "number", message: "置顶排序必须为数字" }
                ]
            }
        };
    },
    computed: {
        showProfit: function () {
            return (
                this.formData.Belong != 3 &&
                this.formData.ChannelList.includes(64)
            );
        },
        showBl: function () {
            return (
                this.formData.Belong == 3 && this.formData.ProductAttribute == 2
            );
        },
        showPower: function () {
            return (this.formData.Belong == 3 && $.inArray(this.TagId, this.formData.TagInfoList) > -1);
        },
        platsource: function () {
            if (this.formData.Belong == 1) {
                return config.productPlats.filter(item => {
                    return item.id !== 4;
                });
            }
            else {
                return config.productPlats;
            }
        },
        subplatsource: function () {
            return config.productSubPlats;
        },
        showSubPlat: function () {
            //只有出境、CCT渠道才有二级渠道
            return this.formData.Belong == 1 && this.formData.ChannelList && this.formData.ChannelList.indexOf(16) >= 0;
        }
    },
    mounted() {
        this.loadDatas();
    },
    watch: {
        "formData.ChannelList": function (val, o) {
            var that = this;
            var subChannels = [];
            if (val && val.indexOf(16) >= 0) {
                if (!that.formData.SubChannels || (that.formData.SubChannels && that.formData.SubChannels.length <= 0)) {
                    that.subplatsource.forEach(function (item) {
                        subChannels.push(item.id)
                    });
                    that.formData.SubChannels = subChannels;
                }
            }
        },
        "formData.SupplierCommitment": function (val, old) {
            let that = this;
            if (val == 1 && that.formData.Belong != 1 && (!that.suppliersource || that.suppliersource.length <= 0)) {
                that.getsuppliersource();
            }
        },
        "showBl": function (val, old) {
            if (val == true && (!this.tagsource || this.tagsource.length <= 0)) {
                this.getBlTags();
            }
        }
    },
    methods: {
        handleCheckAllChange: function (val) {
            var chkChannels = [];
            if (val) {
                this.platsource.forEach(function (item) {
                    chkChannels.push(item.id)
                });
            }
            this.formData.ChannelList = chkChannels;
        },
        supplierfomart: function (item) {
            var typeName = "";
            if (item["ResourceType"] == "1") {
                typeName = "产品编号:";
            } else if (item["ResourceType"] == "2") {
                typeName = "酒店政策:";
            } else if (item["ResourceType"] == "4") {
                typeName = "机票资源:";
            } else if (item["ResourceType"] == "3") {
                typeName = "单品资源:";
            }
            return (
                item.SupplierName +
                item.ResourceName +
                `（${typeName}${item.ResourceId}）`
            );
        },
        /**
         * 获取费用承担供应商
         */
        getsuppliersource: function () {
            var that = this;
            httputils.get('/travel/supergoproduct/ProductBaseInfoExtends/GetProductSuppliers', {
                productId: that.productId
            }).then(result => {
                if (result && result.IsSuccess) {
                    that.suppliersource = result.Result;
                }
            }).catch(err => {
                that.$message({
                    type: 'error',
                    message: '获取优惠费用承担商失败'
                });
            });
        },
        /**
         * 获取百旅标签
         */
        getBlTags: function () {
            let that = this;
            httputils.get('/travel/supergoproduct/ProductBaseInfoExtends/QueryProductBlTag',
                {
                    productId: that.productId
                }).then(result => {
                    if (result && result.TagInfoList) {
                        that.blMax = result.MaxCount;
                        var lstTag = [];
                        if (that.formData.PowerCount > 0) {
                            lstTag.push(that.TagId);
                        }
                        result.TagInfoList.forEach(function (item) {
                            if (
                                item.IsSelect &&
                                $.inArray(item.TagInfoId, lstTag) == -1
                            ) {
                                lstTag.push(item.TagInfoId);
                            }
                        });
                        that.tagsource = result.TagInfoList;
                        that.formData.TagInfoList = lstTag;
                    }
                }).catch(err => {
                    that.$message({
                        type: 'error',
                        message: '获取百旅会权益标签失败'
                    });
                });
        },
        loadDatas: function () {
            var that = this;
            var loading = that.$loading({ lock: true });
            httputils.get("/travel/supergoproduct/ProductBaseInfoExtends/QueryExtends", {
                productId: that.productId
            }).then(function (result) {
                loading.close();
                if (result) {
                    that.formData = Object.assign(
                        that.formData,
                        result
                    );
                    that.formData.IsShowSearch = result.IsShowSearch;
                    that.formData.IsMarketing = result.IsMarketing;
                    that.formData.Belong = result.Belong;
                    that.formData.NewWarehouseProfit = result.NewWarehouseProfit;
                    that.formData.IsCheckRevenue = result.IsCheckRevenue;
                    that.formData.IsCheckHotel = result.IsCheckHotel;
                    if (that.formData.Belong != 1 && that.formData.SupplierCommitment == 1) {
                        that.checkSupplierResource = that.formData.CommitmentSupplierId + "," + that.formData.RatioResourceType + "," + that.formData.RatioResourceId;
                    }
                }
            }).catch(err => {
                console.log(err)
                that.$message({
                    type: 'error',
                    message: '销售配置加载异常'
                });
            });
        },
        saveDatas: function () {
            var that = this;
            this.$refs["formData"].validate(function (valid) {
                if (valid) {
                    if (that.formData.Belong == 3) {
                        if (that.formData.TagInfoList.length > that.blMax) {
                            that.$confirm("百旅会权益最多选择" + that.blMax + "项！", "", {
                                confirmButtonText: "确定",
                                type: "warning",
                                center: true,
                                showCancelButton: false
                            });
                            return;
                        }
                        if ($.inArray(that.TagId, that.formData.TagInfoList) > -1 && that.formData.PowerCount == 0) {
                            that.$confirm("请选择权益下单次数！", "", {
                                confirmButtonText: "确定",
                                type: "warning",
                                center: true,
                                showCancelButton: false
                            });
                            return;
                        }
                    }
                    if (that.formData.Belong == 2 || that.formData.Belong == 3) {
                        if (that.formData.SupplierCommitment == 1) {
                            var arr = that.checkSupplierResource.split(",");
                            if (arr.length != 3) {
                                that.$confirm("请选择优惠费用承担商！", "", {
                                    confirmButtonText: "确定",
                                    type: "warning",
                                    center: true,
                                    showCancelButton: false
                                });
                                return;
                            }
                            that.formData.CommitmentSupplierId = arr[0];
                            that.formData.RatioResourceType = arr[1];
                            that.formData.RatioResourceId = arr[2];
                        }
                    }

                    let loading = that.$loading({ lock: true });
                    httputils.post('/travel/supergoproduct/ProductBaseInfoExtends/Index',
                        that.formData)
                        .then(result => {
                            loading && loading.close();
                            if (result && result.IsSuccess) {
                                if (result.ErrorMsg.length > 0) {
                                    that.$confirm(result.ErrorMsg, "保存成功", {
                                        confirmButtonText: "确定",
                                        type: "warning",
                                        center: true,
                                        showCancelButton: false
                                    });
                                }
                                else {
                                    that.$message({
                                        message: "销售配置信息保存成功！",
                                        type: "success"
                                    });
                                }
                            } else {
                                that.$confirm(result.ErrorMsg, "保存失败", {
                                    confirmButtonText: "确定",
                                    type: "warning",
                                    center: true,
                                    showCancelButton: false
                                });
                            }
                        }).catch(err => {
                            that.$message({
                                type: 'error',
                                message: err
                            });
                            loading && loading.close();
                        });
                }
            });
        }
    }
}
</script>
<style scoped>
[v-cloak] {
  display: none;
}

.el-form-item {
  margin-bottom: 16px;
}

.el-radio__label {
  padding-left: 0px;
}

.el-checkbox-group .el-checkbox {
  margin-left: 0;
  padding-right: 10px;
}

.el-checkbox-group .el-checkbox {
  margin-left: 0;
  padding-right: 10px;
}
</style>



